@import "sass/index";

.box {
  position: absolute;
  bottom: 75%;
  right: 0;
}
.root {
  background: use(--template-footer-background);
  padding: 10px;
  position: relative;
  z-index: 7;

  border-radius: 10px;
  width: 100%;
  max-width: 320px;
  min-width: 200px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.title {
  color: use(--template-modal-title-color);
  white-space: nowrap;
  margin: 0;
  font-size: 1.3rem;
}
.buttonBox {
  width: 100%;
  margin-top: 10px;

  display: flex;
  justify-content: space-around;
}

.disableWrapper {
  background: transparent;
  position: fixed;
  height: 100vh;
  width: 100vw;
  left: 0;
  top: 0;
  z-index: 6;
}

.wrapperNone {
  pointer-events: none;
  display: none;
}

.disableWarning {
  pointer-events: none;
}
